    color: white;
    border-width:0px;
  
}
.tombolb{
    background: -webkit-linear-gradient(left,#270000, #270000);
    background: linear-gradient(to right,#270000, #270000);
  background-repeat: no-repeat;
  background-size: 0 100%;
  -webkit-transition: background-size 0.5s 0s;
  transition: background-size 0.5s 0s;
    width:180px;
    color: #270000;
    box-shadow: 0px 0px 40px 3px #000000a1;
    cursor: pointer;
    font-family: 'Patrick Hand', cursive;
    border-color: #270000;
    height:40px;
    font-weight: bold;
    border-radius: 5px;
    font-size: 18px;
    border-width:2px;

}
.tombolb:hover{
    background-size: 100% 100%;
